body {
  font-family: 'Montserrat', sans-serif;
  
}

.wrapper {
  background: linear-gradient(to right, #e67e22, #e74c3c);
  height: 120vh;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.main {
  height: 90vh;
  /* background: #fff; */
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  margin: 0 auto;
  border-radius: 12px;
  position: relative;
  bottom: 100px;
}

.title-container {
  height: 90vh;
  background: url(/static/media/bg.e0bc83fa.jpg) center center no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #ffffff;
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
  padding-left: 22px;
  padding-right: 22px;
}

.title-container__title  {
  font-size: 40px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Balsamiq Sans', cursive;
  color: #ffffff;
  padding-top: 80px;
}

.title-container__subtitle {
  font-weight: 10;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: 'PT Sans', sans-serif;
  font-size: 20px;
  color: #ffffff;
  align-items: center;

}

.form-container {
  background-color: #2c3e50;
  height: 90vh;
  padding-top: 100px;
  padding-left: 50px;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
 
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f16051;
  width: 30%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

input[type="text"] { 
    outline: none;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
    -webkit-text-fill-color: #fff !important;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #00917c;
  color: #fff;
  font-weight: 100;
  text-transform: inherit;
}

button:hover {
  background-color:#df5e88 ;
}
button:active {
  outline: none;
}

.weather__info {
  width: 60%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}

.weather__key {
  color: #f16051;
  border-bottom: solid 2px rgba(255,255,255,0.06);
  padding: 20px 0 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: #fff;
  font-weight: 200;
}

.weather__error {
  color: #d92027;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}

#Footer{
 
  background-color: #2c3e50;
  color: #fff;
  padding-top: 5px;
  padding-bottom:1px;
  margin-bottom:2px;
  text-align:center;
  
}
.col-2{
  font-size:21px !important;
}
.top-link {
  transition: all .25s ease-in-out;
  position: fixed;
  bottom: 0;
  right: 0;
  display: inline-flex;
   
